.container {
	background-color: #212121;
	width: 100%;
	height: 100%;
	// position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;

	.title {
		margin-top: 2rem;
		text-align: center;
		font-size: 7rem;
		font-weight: 500;
		width: 100%;
		color: white;
	}
	img {
		margin-top: 2rem;
		align-self: flex-end;
	}

	.progressBars {
		display: flex;
		justify-content: space-around;
		margin-bottom: 15rem;
		margin-top: 5rem;
		& > * {
			width: 20%;
		}
		& > div > p {
			color: white;
			font-size: 2rem;
			font-weight: 500;
			text-align: center;
		}
	}

	&_chart {
		.chartTitle {
			font-size: 4rem;
			color: #ff5151;
			span:first-of-type {
				color: white;
				margin-right: 10px;
			}
		}

		.description {
			font-size: 1.5rem;
			color: white;
		}

		.dateLabel {
			color: white;
			margin: 0 auto;
		}

		.buttons {
			margin: 1.5rem 0 5rem 0;
			text-align: center;
			display: inline-block;
			bottom: -80%;
		}
	}
}
